@import url(https://fonts.googleapis.com/css2?family=PT+Sans+Narrow&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Sans+Narrow&display=swap);

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.navbar {
  height: 80px;
  display: flex;
  position: fixed;
  width: 100%;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight: bold;
  z-index: 1;
  background: radial-gradient(#0c0c23, transparent);
}
.navbar_container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.navbar_logo {
  width: 150px;
  margin-left: 2rem;
  cursor: pointer;
  justify-self: start;
}
.navbar_logo--img {
  max-width: 100%;
}
.nav-menu {
  list-style-type: none;
  display: flex;
}
.nav-menu .nav-item {
  padding: 2rem 1rem;
  letter-spacing: 2px;
  text-align: center;
}
.nav-links {
  text-decoration: none;
  color: #fff;
  padding: 0.5rem 1rem;
}
.nav-links:hover {
  color: #ca40ba;
}
.menu-icon {
  margin-right: 2rem;
  font-size: 1.6rem;
  display: none;
  color: #fff;
}

@media (max-width: 960px) {
  /* .navbar {
    position: relative;
  } */
  .nav-menu {
    position: absolute;
    left: -100%;
    display: flex;
    flex-direction: column;
    opacity: 1;
    width: 100%;
    height: 500px;
    top: 80px;
    transition: all 0.5s ease;
  }
  .active {
    opacity: 1;
    background: rgb(32 84 23 / 35%);
    left: 0;
  }
  .menu-icon {
    display: block;
    cursor: pointer;
  }
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.banner {
  padding-left: 100px;
  width: 100%;
  background: url("https://mstevic.com/static/media/heroBg.ea33332d.jpg") no-repeat center/cover;
}
.banner_Info {
  display: flex;
  flex-direction: column;
  color: aliceblue;
  padding: 120px 0;
  min-height: 100vh;
  width: 100%;
}
.banner_description {
  min-height: 256px;
  padding: 50px 0;
}
h2 {
  font-size: 40px;
  padding-left: 12px;
  line-height: 1;
  text-shadow: 4px 4px 1px #ca40ba;
}
span {
  display: block;
  font-size: 21px;
  color: #ca40ba;
  font-weight: 800;
  text-shadow: none;
}

@media(max-width: 600px) {
  h2 {
    font-size: 30px;
  }
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.about {
  font-size: 10px;
  background-image: -webkit-linear-gradient(77deg, #270e4e 64%, rgb(204 182 239 / 41%)55%);
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}
.about_container {
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 3rem;
  text-align: center;
}
.about_name {
  color: #511269;
  font-size: 1rem;
  font-weight: 800;
  letter-spacing: 8px;
  margin-bottom: 2rem;
  padding-top: 3rem;
}
.about_image {
  margin-bottom: 5rem;
  padding-top: 217px;
}
.about_image img {
  border-radius: 50%;
  width: 17rem;
  height: 17rem;
  object-fit: cover;
}
.about_text {
  line-height: 4rem;
  font-size: 1.3rem;
  color: aliceblue;
}
.btn {
  display: inline-block;
  font-size: 2rem;
  background: aliceblue;
  text-decoration: none;
  border-radius: 10px;
  color: aquamarine;
  margin-bottom: 2rem;
  transition: background 0.5s;
}
.btn:hover {
  background: bisque;
}
:root {
  --primary: #fe58a0;
  --text: #363636;
  --light: #888888;
}
.skill {
  background: rgb(234 224 249);
}
.title {
  font-size: 44px;
  padding: 50px;
  text-align: center;
  text-transform: uppercase;
  color:#ca40ba;
  line-height: 1;
  text-shadow: 4px 4px 1px #270e4e;
}
.main {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1024px;
  margin: 0 auto;
}
.main .set {
  padding: 2rem;
  background-color: white;
  border-radius: 1.6rem;
  width: 33%;
  min-height: 30rem;
  margin: 2rem 5px 10rem 5px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-transform: uppercase;
}
.main .set:hover {
  box-shadow: 0px 12px 16px #363636;
  box-shadow: 0px 12px 16px var(--text);
}
.set span {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 8rem;
  height: 8rem;
  font-size: 4rem;
  background-color: #ca40ba;
  color: white;
  border-radius: 35%;
}
.set > header {
  text-align: center;
  color: #ca40ba;
  padding-block: 24px;
  font-size: 24px;
}
.list {
  padding-block: 8px;
  display: flex;
  justify-content: space-between;
  width: 100%;
}

@media (max-width: 600px) {
  .main {
    display: flex;
    flex-direction: column;
  }
  .main .set {
    width: 60%;
    margin: 10px 5px;
  }
}
.experiences {
  background: #270e4e;
}
.experiences_container {
  max-width: 1024px;
  margin: 0 auto;
}
.experiences_container section {
  display: flex;
}
.experiences_image {
  width: 30%;
  padding-top: 5rem;
}
.experiences_image img {
  max-width: 100%;
}
.experiences_content {
  color: white;
  width: 70%;
  padding-left: 5rem;
  letter-spacing: 2px;
  font-family: 'PT Sans Narrow', sans-serif;
  padding-bottom: 5rem;
}
.experiences_content strong {
  color: #ca40ba;
}
.experiences_content--title {
  text-align: center;
  padding: 2rem 0;
  line-height: 2;
}
.experiences_item {
  padding: 1rem 0;
}
.contact {
  background: #eae0f9;
}
.contact_content {
  text-align: center;
  max-width: 960px;
  margin: 0 auto;
  letter-spacing: 1px;
  font-family: 'PT Sans Narrow', sans-serif;
  padding: 0 2rem;
}
.contact_main {
  width: 100%;
  display: flex;
  justify-content: space-around;
  padding: 2rem;
}
.contact_icon {
  display: flex;
  justify-content: space-between;
  box-shadow: 0px 12px 16px #363636;
  padding: 0.5rem;
}
.contact_icon span {
  padding-left: 6px;
}
